* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: monospace, Arial, Helvetica, sans-serif;
}

body {
    transition: .4s;
}

main {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

nav {
    position: absolute;
    top: 20px;
    right: 40px;
}

.switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 24px;
    z-index: 999;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 34px;
}

.slider span:not(class) {
    padding: 0 5px;
    font-size: .8em;
}

.slider:before {
    position: absolute;
    content: '';
    height: 20px;
    width: 20px;
    left: 2px;
    bottom: 2px;
    background-color: #fff;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: #2196F3;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

.slider span:not(class) {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

.dark-mode {
    background-color: #282d35;
    color: #fff;
}

.container {
    width: 80%;
    height: 650px;
    border: 25px solid #034a61;
    border-radius: 10px;
    padding: 0 20px;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.die-container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 50px;
}

.die-face {
    width: 70px;
    height: 60px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    border: 3px solid black;

    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: black;
}

.die-num {
    font-size: 2rem;
}

.roll-btn {
    padding: 10px 35px;
    border: none;
    background-color:#5b47da;
    color: #fff;
    font-size: 1.5rem;
    border-radius: 10px;
    cursor: pointer;
}

.roll-btn:active {
    box-shadow: inset 1px 1px 10px rgba(0, 0, 0, 0.7);
}

.header {
    text-align: center;
    font-size: 2rem;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.header > p {
    font-size: 1.3rem;
    width: 70%;
}

@media (max-width: 768px) {
    .die-container {
        gap: 30px;
        grid-template-columns: repeat(4, 1fr);
    }

    .header > p {
        font-size: 1rem;
    }
}

@media (max-width: 600px) {
    .die-container {
        gap: 10px;
        grid-template-columns: repeat(3, 1fr);
    }

    .header > p {
        width: 100%;
    }
}